@media screen and (min-width: 1366px) {
  .mv {
    height: calc(100vh - 6.8125rem);
    max-height: 50rem;
  }
}

.mv__swiper {
  width: 100%;
  height: 100%;
  max-width: calc(100% - 2.5rem);
  border-radius: 10px;
}
@media screen and (min-width: 768px) {
  .mv__swiper {
    max-width: calc(100% - 9.5rem);
  }
}

.mv__slide {
  height: 100%;
  border-radius: 10px;
}

.mv__slide-img-wrap {
  height: 100%;
}

.mv__slide-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
  aspect-ratio: 350/290;
}
@media screen and (min-width: 768px) {
  .mv__slide-img {
    aspect-ratio: 1214/683;
  }
}

.mv__swiper .swiper-slide.swiper-slide-active .swiper-img,
.mv__swiper .swiper-slide.swiper-slide-duplicate-active .swiper-img,
.mv__swiper .swiper-slide.swiper-slide-prev .swiper-img {
  animation: scale-sp 8s linear 0s normal both;
  transform-origin: left top;
}
@media screen and (min-width: 768px) {
  .mv__swiper .swiper-slide.swiper-slide-active .swiper-img,
  .mv__swiper .swiper-slide.swiper-slide-duplicate-active .swiper-img,
  .mv__swiper .swiper-slide.swiper-slide-prev .swiper-img {
    animation: scale 8s linear 0s normal both;
  }
}

@keyframes scale {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.04);
  }
}
@keyframes scale-sp {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.06);
  }
}
