/* ===============================================
# フェードアニメーション：IntersectionObserver＆GSAP用
=============================================== */
/* フェード */
.js-fade-in {
  opacity: 0;
  transition: opacity 1s ease-in-out;
}
.js-fade-in.is-active {
  opacity: 1;
}

/* 上方向にスライド */
.js-fade-up {
  opacity: 0;
  translate: 0 20px;
  transition: opacity 1s ease-in-out, translate 1s ease-in-out;
}
.js-fade-up.is-active {
  opacity: 1;
  translate: 0 0;
}

/* 左方向にスライド */
.js-slide-left {
  opacity: 0;
  translate: -10px 0;
  transition: opacity 1s ease-in-out, translate 1s ease-in-out;
}
.js-slide-left.is-active {
  opacity: 1;
  translate: 0 0;
}

/* 下方向にスライド */
.js-fade-down {
  opacity: 0;
  translate: 0 -20px;
  transition: opacity var(--duration-long) ease, translate var(--duration-long) ease;
}
.js-fade-down.is-active {
  opacity: 1;
  translate: 0 0;
}

/* 右方向にスライド */
.js-slide-right {
  opacity: 0;
  translate: 10px 0;
  transition: opacity var(--duration-long) ease, translate var(--duration-long) ease;
}
@media screen and (min-width: 768px) {
  .js-slide-right {
    translate: 20px 0;
  }
}
.js-slide-right.is-active {
  opacity: 1;
  translate: 0 0;
}

.js-scaleImg {
  overflow: hidden;
}
.js-scaleImg img {
  scale: 1.05;
  transition: scale 1s ease-in-out, opacity 1s ease-in-out;
  opacity: 0;
}
.js-scaleImg.is-active img {
  scale: 1;
  opacity: 1;
}

/* クリップパス */
.js-clip-img {
  overflow: hidden;
}
.js-clip-img img {
  scale: 1.3;
  transition: scale 1s var(--ease-smooth-bounce), clip-path 1s var(--ease-smooth-bounce);
  clip-path: inset(0 100% 0 0);
}
.js-clip-img.is-active img {
  clip-path: inset(0 0 0 0);
  scale: 1;
}
@media screen and (min-width: 768px) {
  .js-clip-img img {
    transition: scale 1.2s var(--ease-smooth-bounce), clip-path 1.2s var(--ease-smooth-bounce);
  }
}

