/* ===============================================
# ドロワーメニュー
=============================================== */
.drawer {
  position: fixed;
  z-index: var(--z-index-drawer);
  top: 4.3125rem;
  right: 0;
  left: 0;
  width: 100%;
  height: 0;
  background: var(--white);
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  transition: height 0.5s ease-in-out, opacity 0.5s ease-in-out, visibility 0s linear 0.5s;
  max-width: calc(100% - 22px);
  margin-inline: auto;
  border-radius: 0 0 1.5625rem 8.125rem;
}
.drawer.is-open {
  opacity: 1;
  visibility: visible;
  transition: height 0.5s ease-in-out, opacity 0.5s ease-in-out, visibility 0s linear 0s;
}

.drawer__body {
  width: 100%;
  height: fit-content;
  padding-block: 2.875rem 4.875rem;
  padding-inline: 4.375rem;
  margin-inline: auto;
}

.drawer__list {
  display: flex;
  flex-direction: column;
  gap: 1.1875rem;
  justify-content: center;
}

.drawer__item {
  width: 100%;
  text-align: center;
}

.drawer__link {
  font-size: 0.875rem;
  font-weight: var(--font-weight-normal);
  letter-spacing: 0.1em;
  padding: 0.1875rem 0.75rem;
  border-radius: calc(infinity * 1px);
}
@media (hover: none) {
  .drawer__link:active {
    background-color: var(--main-yellow);
    color: var(--white);
  }
}

.drawer__link.current {
  background-color: var(--main-yellow);
  color: var(--white);
}

.is-open .drawer__item {
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.7s ease-in-out forwards;
}

.is-open .drawer__item:nth-child(1) {
  animation-delay: 0.2s;
}

.is-open .drawer__item:nth-child(2) {
  animation-delay: 0.3s;
}

.is-open .drawer__item:nth-child(3) {
  animation-delay: 0.4s;
}

.is-open .drawer__item:nth-child(4) {
  animation-delay: 0.5s;
}

.is-open .drawer__item:nth-child(5) {
  animation-delay: 0.6s;
}

.is-open .drawer__item:nth-child(6) {
  animation-delay: 0.7s;
}

.is-open .drawer__item:nth-child(7) {
  animation-delay: 0.8s;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.body-hidden {
  overflow: hidden;
}

.link-banner {
  display: grid;
  grid-template-columns: 1fr;
}
@media screen and (min-width: 768px) {
  .link-banner {
    grid-template-columns: 360fr 429fr;
    align-items: center;
    max-width: 789px;
  }
}
@media (any-hover: hover) {
  .link-banner:hover .link-banner__img {
    scale: 1.05;
  }
  .link-banner:hover .link-banner__img-text:after {
    transform: translateX(0.3125rem);
  }
}

.link-banner__content {
  background-color: var(--white);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.9375rem;
  padding: 2.875rem 1.875rem 2.5625rem 1.875rem;
}
@media (max-width: 767px) {
  .link-banner__content {
    align-items: center;
    border-radius: 10px 50px 0 0;
    background: #FFF;
  }
}
@media screen and (min-width: 768px) {
  .link-banner__content {
    gap: 1.0625rem;
    padding: 1.25rem 1.875rem 1.25rem 3.3125rem;
  }
}

.link-banner__text {
  font-size: 0.9375rem;
  font-weight: var(--font-weight-regular);
  line-height: 2;
  letter-spacing: 0.1em;
}

.link-banner__img-wrap {
  position: relative;
  overflow: hidden;
  border-radius: 0 0 10px 10px;
}
@media screen and (min-width: 768px) {
  .link-banner__img-wrap {
    border-radius: 0 50px 10px 0;
  }
}

.link-banner__img {
  height: auto;
  object-fit: cover;
  aspect-ratio: 350/187;
  transition: scale var(--duration-base) ease-in-out;
  border-radius: 0 0 10px 10px;
}
@media screen and (min-width: 768px) {
  .link-banner__img {
    aspect-ratio: 429/199;
    border-radius: 0 50px 10px 0;
  }
}

.link-banner__img-text {
  position: absolute;
  bottom: 1.75rem;
  right: 1.3125rem;
  font-size: 0.9375rem;
  font-weight: var(--font-weight-regular);
  letter-spacing: 0.1em;
  border-radius: 20px;
  border: 2px solid var(--main-yellow);
  background-color: var(--white);
  display: flex;
  align-items: center;
  width: fit-content;
  padding: 0.28125rem 1.16875rem 0.28125rem 1.10625rem;
  gap: 0.75rem;
  white-space: nowrap;
}
@media screen and (min-width: 768px) {
  .link-banner__img-text {
    bottom: 1.6875rem;
    right: 1.4375rem;
  }
}

.link-banner__img-text:after {
  content: "";
  display: inline-block;
  width: 0.4375rem;
  background-color: var(--main-yellow);
  border-radius: calc(infinity * 1px);
  aspect-ratio: 1/1;
  transition: transform var(--duration-base) ease-in-out;
}

.link-btn {
  font-size: 0.9375rem;
  font-weight: var(--font-weight-regular);
  letter-spacing: 0.1em;
  border-radius: 20px;
  border: 2px solid var(--main-yellow);
  display: flex;
  align-items: center;
  width: fit-content;
  padding: 0.28125rem 1.34375rem 0.28125rem 1.34375rem;
  gap: 0.75rem;
  transition: background-color var(--duration-base) ease-in-out, color var(--duration-base) ease-in-out;
}
@media (any-hover: hover) {
  .link-btn:hover {
    background-color: var(--main-yellow);
    color: var(--white);
  }
  .link-btn:hover:after {
    background-color: var(--white);
    transform: translateX(0.1875rem);
  }
}

.link-btn:after {
  content: "";
  display: inline-block;
  width: 0.4375rem;
  background-color: var(--main-yellow);
  border-radius: calc(infinity * 1px);
  aspect-ratio: 1/1;
  transition: background-color var(--duration-base) ease-in-out, transform var(--duration-base) ease-in-out;
}

.link-btn02 {
  font-family: var(--font-family-second);
  font-size: 1rem;
  font-weight: var(--font-weight-normal);
  letter-spacing: 0.1em;
  border-radius: calc(infinity * 1px);
  background-color: var(--bg-color);
  border: 2px solid var(--main-yellow);
  padding: 1.0625rem 0.9375rem;
  min-width: 17.5rem;
  text-align: center;
  position: relative;
  width: fit-content;
  transition: background-color var(--duration-base) ease-in-out, color var(--duration-base) ease-in-out;
}
@media (any-hover: hover) {
  .link-btn02:hover {
    background-color: var(--main-yellow);
    color: var(--white);
  }
}
@media (any-hover: hover) {
  .link-btn02:hover::after {
    background-color: var(--white);
    right: 1.6875rem;
  }
}
.link-btn02::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 2.0625rem;
  transform: translateY(-50%);
  width: 0.5375rem;
  background-color: var(--main-yellow);
  border-radius: calc(infinity * 1px);
  aspect-ratio: 1/1;
  transition: background-color var(--duration-base) ease-in-out, right var(--duration-base) ease-in-out;
  will-change: right, background-color;
}

.link-btn02.link-btn02--yellow {
  background-color: var(--main-yellow);
  color: var(--white);
}
.link-btn02.link-btn02--yellow::after {
  background-color: var(--bg-color);
}
@media (any-hover: hover) {
  .link-btn02.link-btn02--yellow:hover {
    background-color: var(--bg-color);
    color: var(--main-yellow);
  }
  .link-btn02.link-btn02--yellow:hover::after {
    background-color: var(--main-yellow);
  }
}

.news-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.news-list__link {
  display: block;
  background-color: var(--bg-color);
  padding: 1.5rem 1.625rem 1.5rem 0.6875rem;
  display: grid;
  grid-template-columns: max-content 1fr;
  gap: 2.625rem;
}
@media screen and (min-width: 768px) {
  .news-list__link {
    padding: 1.625rem 0.9375rem 1.625rem 1.8125rem;
    align-items: center;
  }
}
@media (any-hover: hover) {
  .news-list__link:hover .news-list__title {
    color: #9d2020;
  }
  .news-list__link:hover .news-list__date {
    color: #9d2020;
  }
}

.news-list__date {
  font-size: 0.8125rem;
  font-weight: var(--font-weight-normal);
  line-height: 1.923;
  letter-spacing: 0.05em;
  transition: color var(--duration-base) ease-in-out;
}
@media screen and (min-width: 768px) {
  .news-list__date {
    line-height: 1.3;
  }
}

.news-list__title {
  font-size: 0.9375rem;
  font-weight: var(--font-weight-normal);
  line-height: 1.667;
  letter-spacing: 0.05em;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  line-clamp: 4;
  overflow: hidden;
  transition: color var(--duration-base) ease-in-out;
}
@media screen and (min-width: 768px) {
  .news-list__title {
    -webkit-line-clamp: 2;
    line-clamp: 2;
    line-height: 1.3;
  }
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 0.8125rem;
  row-gap: 0.9375rem;
  flex-wrap: wrap;
}
.pagination .page-link,
.pagination .current-page {
  color: var(--main-red);
  font-family: var(--font-family-second);
  font-size: 15px;
  font-weight: var(--font-weight-bold);
  aspect-ratio: 1/1;
  width: 2.5rem;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pagination .dots + .page-link,
.pagination .dots + .current-page {
  margin-left: -0.625rem;
}

.pagination .page-link + .dots,
.pagination .current-page + .dots {
  margin-left: -0.625rem;
}

.pagination .page-link {
  border: 2px solid var(--white);
  transition: background-color var(--duration-base) ease, color var(--duration-base) ease, border var(--duration-base) ease;
}
@media (any-hover: hover) {
  .pagination .page-link:hover {
    background-color: var(--main-red);
    color: var(--white);
    border: 2px solid var(--main-red);
  }
}

.pagination .current-page {
  border: 2px solid var(--main-red);
  background-color: var(--main-red);
  color: var(--white);
}

.pagination .dots {
  font-family: var(--font-family-second);
  font-size: 1.5625rem;
  font-weight: var(--font-weight-bold);
  letter-spacing: 0.05em;
}

.pagination .prev,
.pagination .next {
  color: var(--main-red);
  text-align: center;
  font-size: 14px;
  font-weight: var(--font-weight-bold);
  letter-spacing: 0.05em;
  aspect-ratio: 1/1;
  width: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform var(--duration-base) ease;
}

@media (any-hover: hover) {
  .pagination .prev:hover {
    transform: translateX(-5px);
  }
}

@media (any-hover: hover) {
  .pagination .next:hover {
    transform: translateX(5px);
  }
}

.post-navigation {
  margin-inline: auto;
  max-width: var(--max-width);
  padding-inline: var(--inner-padding);
  display: flex;
  gap: 1.475rem;
}
@media (max-width: 767px) {
  .post-navigation {
    flex-direction: column;
    align-items: center;
  }
}
@media screen and (min-width: 768px) {
  .post-navigation {
    max-width: calc(959px + var(--inner-padding) * 2);
    justify-content: space-between;
    gap: 3.125rem;
  }
}

.post-nav {
  font-family: var(--font-family-second);
  font-size: 1rem;
  display: inline-block;
  width: fit-content;
  font-weight: var(--font-weight-normal);
  letter-spacing: 0.1em;
  border-radius: calc(infinity * 1px);
  background-color: var(--bg-color);
  border: 2px solid var(--main-yellow);
  padding: 1.0625rem 0.9375rem;
  min-width: 17.5rem;
  text-align: center;
  position: relative;
  transition: background-color var(--duration-base) ease-in-out, color var(--duration-base) ease-in-out;
}
@media (any-hover: hover) {
  .post-nav:hover {
    background-color: var(--main-yellow);
    color: var(--white);
  }
  .post-nav:hover::after {
    background-color: var(--white);
  }
  .post-nav.post-nav--prev:hover::after {
    left: 1.8125rem;
  }
  .post-nav.post-nav--next:hover::after {
    right: 1.8125rem;
  }
}
.post-nav::after {
  content: "";
  position: absolute;
  top: 52%;
  translate: 0 -50%;
  width: 0.625rem;
  aspect-ratio: 7/15;
  background-color: var(--main-yellow);
  -webkit-mask-image: url("../images/common/arrow-right.svg");
  mask-image: url("../images/common/arrow-right.svg");
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  -webkit-mask-size: contain;
  mask-size: contain;
  transition: background-color var(--duration-base) ease-in-out, left var(--duration-base) ease-in-out, right var(--duration-base) ease-in-out;
  will-change: right, left, background-color;
}

.post-nav--prev::after {
  left: 2.0625rem;
  transform: scale(-1, 1);
}

.post-nav--next::after {
  right: 2.0625rem;
}

.section-title {
  font-family: var(--font-family-second);
  font-size: 1.5625rem;
  font-weight: var(--font-weight-normal);
  line-height: 1.1;
  letter-spacing: 0.1em;
}
@media screen and (min-width: 768px) {
  .section-title {
    font-size: 1.875rem;
  }
}

.sub-mv {
  margin-top: 7.4375rem;
}
@media screen and (min-width: 768px) {
  .sub-mv {
    margin-top: 11rem;
  }
}

.sub-mv__title {
  text-align: center;
  font-family: var(--font-family-second);
  font-size: 1.5625rem;
  font-weight: var(--font-weight-normal);
  line-height: 1.3;
  letter-spacing: 0.1em;
}
@media screen and (min-width: 768px) {
  .sub-mv__title {
    font-size: 1.875rem;
  }
}

.to-top {
  position: fixed;
  right: 1.25rem;
  bottom: 4.375rem;
  z-index: var(--z-index-to-top);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out;
}
@media screen and (min-width: 768px) {
  .to-top {
    bottom: 5.625rem;
    right: 4.75rem;
  }
}
.to-top.js-active {
  opacity: 1;
  visibility: visible;
}

.to-top__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.5625rem;
  border: solid 2px var(--main-red);
  padding: 1.125rem 0.25rem;
  border-radius: calc(infinity * 1px);
}
@media (any-hover: hover) {
  .to-top__inner:hover .to-top__icon {
    transform: translateY(-5px);
  }
}

.to-top__icon {
  width: 0.4375rem;
  aspect-ratio: 1/1;
  background-color: var(--main-red);
  border-radius: calc(infinity * 1px);
  display: inline-block;
  transition: transform var(--duration-base) ease-in-out;
}

.to-top__text {
  writing-mode: vertical-rl;
  font-size: 0.9375rem;
  font-weight: var(--font-weight-regular);
  letter-spacing: 0.1em;
}